.count--component {
    display: flex;
    width: 90px;
    height: 25px;

    &.responsive {
        width: 40px;
        height: 15px;
        font-size: 12px;

        .result {
            padding: 0 0.3rem;
        }

        @media (min-width: 768px) {
            width: 75px;
            height: 23px;
            font-size: 14px;

            .result {
                padding: 0;
            }
            .minus,
            .plus {
                img {
                    width: 16px;
                }
            }
        }
    }

    .minus,
    .result,
    .plus {
        flex: 1;
        @include flexCentered();
        height: 100%;
    }

    .result {
        flex: 1.5;
    }

    .minus,
    .plus {
        cursor: pointer;
        background-color: rgb(255, 255, 255);
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        transition: 0.2s;

        img {
            width: 18px;
            opacity: 0.3;
        }

        &:hover {
            box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.4);

            img {
                opacity: 0.8;
            }
        }
    }
}
