section#views-cancel,
section#views-order {
    .basic-container {
        max-width: 600px;
    }

    h2.module_pagetitle {
        padding-bottom: 0;
    }

    .list-bundle-wrapper {
        width: 95%;
        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        margin: 15px auto;

        @media (min-width: 768px) {
            width: initial;
            padding: 0 12px;
        }
    }

    .order-number-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 13px 0px 20px;
        font-size: 14px;
        font-weight: 500;
        color: #333;

        span {
            padding-left: 7px;
            color: #888888;
            font-size: 13px;
            font-weight: 400;
        }

        time {
            display: block;
            margin-top: 8px;
            font-size: 0.8em;
            color: #888;
        }

        button {
            width: 50px;
            height: 24px;
            font-size: 11px;
            background-color: #fff;
            color: #1f7ce6;
            border: 1px solid #1f7ce6;
        }
    }

    ul.list-box {
        padding: 0 10px;

        li {
            display: flex;
            padding: 20px 0;
            border-bottom: 1px solid #d6deeb;

            &:last-child {
                border: 0;
            }

            span.count {
                &::before {
                    content: '수량';
                    padding-right: 5px;
                }
            }
        }
    }

    .checkbox-wrap {
        display: flex;
        align-items: center;
    }

    figure.image-wrap {
        padding-left: 10px;

        img {
            width: 100px;
            height: 100px;
            object-fit: cover;
            border-radius: 4px;
        }
    }

    .info-wrap {
        flex: 1;
        font-size: 13px;
        padding-left: 15px;
    }

    .product-bundle {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @media (min-width: 768px) {
            flex-direction: row;
            align-items: center;
        }
    }

    .product-name {
        font-weight: 500;
    }

    .product-price {
        .name {
            font-size: 12.5px;
            color: #666;
            margin-bottom: 6px;

            @media (min-width: 768px) {
                margin-bottom: 8px;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        p {
            display: inline-block;
            font-size: 13px;
            font-weight: 500;
            color: #1f7ce6;
            padding-left: 5px;
        }
    }

    .product-time {
        color: #888;
        font-size: 0.9em;
    }

    .more-btn {
        width: 200px;
        background-color: white;
        color: #1f7ce6;
        border: 1px solid #1f7ce6;
        margin: 20px auto 0;
    }
}

section#views-detail {
    .list-bundle-wrapper {
        box-shadow: initial;
        width: 100%;
    }

    .number-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 13px 15px 13px 10px;
        margin: 24px 0 35px;
        background-color: #d6deeb;
        font-size: 15px;
        font-weight: 500;

        span {
            padding-left: 7px;
            font-size: 0.9em;
            font-weight: 400;
            color: #888;
        }

        time {
            font-size: 0.8em;
            color: #888;
        }
    }

    article.order-summary-wrapper {
        border-bottom: initial;
    }

    table {
        border-collapse: collapse;
        width: 100%;

        tr {
            height: 50px;
            font-size: 12px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);

            &:last-child {
                border-bottom: initial;
            }

            @media (min-width: 768px) {
                font-size: 13px;
            }

            td {
                text-align: center;
                vertical-align: middle;

                &:first-child {
                    background-color: rgb(245, 245, 245);
                    min-width: 100px;
                }

                &:last-child {
                    text-align: left;
                    padding: 0 20px;

                    @media (min-width: 768px) {
                        padding-left: 30px;
                    }
                }
            }
        }
    }
}
