section#category {
    .condition-box {
        padding: 12px 24px;
        font-size: 12px;
        color: #888;

        span {
            cursor: pointer;

            &:first-child ~ span {
                &::before {
                    content: '•';
                    color: #ccc !important;
                    padding-right: 10px;
                    margin-left: 10px;
                }
            }

            &.active {
                color: #000;
            }
        }
    }

    ul.products-list {
        display: flex;
        flex-wrap: wrap;
        padding: 14px 10px;

        li {
            width: 50%;
            display: flex;
            justify-content: center;
            margin-bottom: 30px;

            &:nth-last-child(1),
            &:nth-last-child(2) {
                margin-bottom: 0;
            }

            @media (min-width: 768px) {
                width: 33.3333333%;

                &:nth-last-child(1),
                &:nth-last-child(2),
                &:nth-last-child(3) {
                    margin-bottom: 0;
                }
            }
            @media (min-width: 1024px) {
                width: 25%;

                &:nth-last-child(1),
                &:nth-last-child(2),
                &:nth-last-child(3),
                &:nth-last-child(4) {
                    margin-bottom: 0;
                }
            }

            img {
                width: 140px;
                height: 140px;
                object-fit: cover;
                border-radius: 4px;
                box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);

                @media (min-width: 768px) {
                    width: 200px;
                    height: 200px;
                }
            }

            figcaption {
                font-size: 14px;
                line-height: 1.5;
                color: #333;
                padding: 8px 0 10px 5px;
            }

            strong {
                width: 140px;
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                margin-bottom: 4px;
                font-weight: bold;

                @media (min-width: 768px) {
                    width: 200px;
                }
            }

            .name {
                font-size: 0.9em;
                opacity: 0.9;
            }

            p {
                opacity: 0.7;
                display: inline-block;
                padding-left: 10px;
            }

            span {
                margin-left: 3px;
            }
        }
    }
}
