section#mypage {
    .outer-wrapper {
        display: flex;
        flex-direction: column;

        @media (min-width: 768px) {
            flex-direction: row;
        }
    }

    article.my-information-wrapper {
        background-color: #ebf6ff;
        padding: 20px 20px 20px 20px;
        position: relative;
        border-bottom: 1px solid #dde9fa;
        flex: 1;
        max-width: 600px;
    }

    strong.info_id {
        display: block;
        font-size: 22px;
        color: #000;
        word-break: break-all;
        line-height: 1.3em;
        margin-bottom: 10px;

        @media (min-width: 768px) {
            margin-bottom: 18px;
        }
    }

    span.info_address,
    span.info_name,
    span.info_number,
    span.info_email {
        display: block;
        font-size: 12px;
        color: #808991;
        line-height: 1.5em;

        @media (min-width: 768px) {
            margin-bottom: 5px;
        }
    }

    a.btn-logout {
        display: block;
        padding: 7px 15px;
        border-radius: 4px;
        height: auto;
        line-height: 1em;
        position: absolute;
        right: 20px;
        top: 50%;
        margin-top: -15px;
        font-size: 12px;
        font-weight: 700;
        color: #636c73;
        border: 2px solid #d6deeb;
    }

    .my-dashboard-wrapper {
        min-width: 350px;
        padding: 0 10px 20px 10px;

        @media (min-width: 768px) {
            padding-left: 50px;
        }
    }

    .dashboard {
        .bundle {
            display: flex;
            flex-direction: column;
            align-items: center;
            font-size: 13px;
            padding: 24px 0 18px 0;

            @media (min-width: 768px) {
                padding: 10px 0 18px 0;
            }

            p {
                font-weight: 700;
                margin-top: 7px;
                color: #000;
            }

            .had-volume {
                color: #1f7ce6;
                margin-top: 12px;
                font-size: 19px;
                line-height: 14px;
                font-weight: 400;
            }
        }

        .input-button {
            margin-top: 20px;
        }
    }

    article.list-wrapper {
        max-width: 600px;
        padding-top: 10px;

        @media (min-width: 768px) {
            padding-top: 50px;
        }

        div {
            height: 40px;
            background-color: #e6e8eb;
        }

        ul {
            background-color: #fff;
            list-style-type: none;
            padding: 0;
            margin: 0;

            li {
                border-bottom: 1px solid #e6e8eb;

                a {
                    display: block;
                    padding: 18px 30px 18px 15px;
                    position: relative;
                    font-size: 14px;
                    color: #000;
                    font-weight: 500;
                    background-image: url('https://icongr.am/feather/chevron-right.svg?size=18&color=888888');
                    background-repeat: no-repeat;
                    background-position: 97% center;
                }
            }
        }
    }
}
