section#product {
    .detail-header {
        padding-top: 24px;
        display: flex;
        flex-direction: column;

        @media (min-width: 768px) {
            flex-direction: row;
            justify-content: center;
            padding: 0 16px;
        }

        @media (min-width: 1024px) {
            padding: 0;
        }
    }

    .product-thumbnail-wrapper {
        figure {
            @include flexCentered();

            img {
                width: 350px;
                height: 350px;
                object-fit: cover;
                border-radius: 4px;
                box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.5);

                @media (min-width: 1024px) {
                    width: 400px;
                    height: 400px;
                }
            }
        }
    }

    .product-info-wrapper {
        position: relative;
        flex: 1;

        padding: {
            left: 30px;
            top: 30px;
        }

        @media (min-width: 768px) {
            padding: {
                left: 40px;
            }
        }
        @media (min-width: 1024px) {
            padding: {
                left: 70px;
            }
        }
    }

    .info-box {
        position: relative;
        display: flex;
        flex-direction: column;
        font-size: 16px;

        strong {
            // width: 140px;
            display: block;
            // overflow: hidden;
            // text-overflow: ellipsis;
            // white-space: nowrap;
            margin-bottom: 17px;
            font-weight: bold;
            font-size: 1.3em;

            @media (min-width: 768px) {
                font-size: 1.5em;
                margin-bottom: 44px;
                // width: 200px;
            }
            @media (min-width: 1024px) {
                margin-bottom: 64px;
                // width: 200px;
            }
        }

        .name {
            font-size: 0.9em;
            opacity: 0.9;
            margin-bottom: 10px;

            &:nth-of-type(2) {
                margin-bottom: 17px;
            }

            @media (min-width: 768px) {
                font-size: 1em;
                width: 200px;
                margin-bottom: 27px;
            }
        }

        p {
            opacity: 0.7;
            display: inline-block;
            padding-left: 10px;
        }

        span {
            margin-left: 3px;
        }

        .count--component {
            margin-bottom: 20px;

            @media (min-width: 768px) {
                margin-bottom: 20px;
            }
            @media (min-width: 1024px) {
                margin-bottom: 40px;
            }
        }

        .total {
            display: flex;
            align-items: center;
            font-size: 14px;

            .title {
                color: rgb(62, 62, 62);
            }

            .value {
                font-size: 20px;
                font-weight: 500;
                color: #1f7ce6;
            }
        }
    }

    .buttons-box {
        display: flex;
        padding-top: 28px;

        @media (min-width: 768px) {
            position: absolute;
            bottom: 20px;
            padding-top: 0px;
        }

        button {
            height: 40px;
            transition: 0.2;

            &:hover {
                box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
            }
        }

        .cart-btn {
            width: 40px;
            border: 1px solid #999999;
            border-radius: 4px;
            margin-right: 10px;
        }

        .buy-btn {
            width: 120px;
        }
    }

    article.detail-block {
        padding-top: 50px;

        @media (min-width: 768px) {
            padding-top: 100px;
        }

        .scrolling-wrap {
            height: 40px;
            display: flex;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            font-size: 14px;

            a {
                flex: 1;
                border: 1px solid rgba(0, 0, 0, 0.1);
                border-bottom: unset;
                display: flex;
                justify-content: center;
                align-items: center;
                // border-radius: 4px;

                &:first-child ~ a {
                    border-left: unset;
                }

                &.active {
                    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.2);
                    background-color: #f7fafc;
                }

                @media (min-width: 768px) {
                    flex: unset;
                    width: 160px;
                    height: 100%;
                }
            }
        }

        .contents-box {
            padding: 14px 0 44px;

            img {
                width: 100%;
            }
        }

        .caution-box {
            .subOrderInfo {
                font-size: 12px;

                @media (min-width: 1024px) {
                    font-size: 13px;
                }

                dl {
                    margin-bottom: 40px;

                    &:first-child dd {
                        margin-top: 0;

                        table {
                            margin: 0;
                        }
                    }

                    dd {
                        color: #444;
                        margin-top: 20px;
                        line-height: 20px;
                    }
                }

                table {
                    text-indent: 0;
                    width: 100%;
                    margin: 35px 0;
                    text-align: center;
                    word-break: break-all;
                    border-collapse: collapse;
                    border-spacing: 0;
                }

                caption {
                    display: block;
                    text-indent: -9999em;
                    overflow: hidden;
                    height: 0;
                    font-size: 0;
                    line-height: 0;
                }

                tr {
                    height: 100px;
                    border-bottom: 1px solid #ddd;

                    &:first-child th,
                    &:first-child td {
                        border-top: 2px solid #444;
                    }

                    th {
                        padding: 5px 10px;
                        min-width: 100px;
                        height: 50px;
                        background-color: #f9f9f9;
                        color: #111;
                        border-bottom: 1px solid #ddd;
                        border-right: 1px solid #ddd;

                        @media (min-width: 768px) {
                            min-width: 200px;
                        }
                    }

                    td {
                        color: #777;
                        padding: 20px;
                        text-align: left;

                        &:last-child {
                            border-right: 0;
                        }
                    }
                }
            }
        }
    }
}
